@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i);
html, body {
    overflow-x: hidden;
    max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



/*Font CSS*/

    @font-face {
        font-family: GilroyExtraBold;
        src: url(/guillermoroman/static/media/Gilroy-ExtraBold.0f6e082f.otf) format("opentype");
    }

    @font-face {
        font-family: GilroyLight;
        src: url(/guillermoroman/static/media/Gilroy-Light.c62aded7.otf) format("opentype");
    }
/*End Font Css*/

/*Header CSS*/

    .initialRedBox{
    	width:50%;
        height: 120vh;
    	background-color: #ff725c;
    	display: inline-block;
    	transition: width 0.9s ease 0ms;
    	z-index: -5;
    }

    .expandRedBox {
    	width: 100%;
    }

    .welcomeMessage {
        font-family: GilroyExtraBold;
        font-size: 3vw;
        position: absolute;
        top: 60%;
        left: 25%;
        width: 30%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }

    .helloInitial {
        font-family: GilroyExtraBold;
        font-size: 7vw;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        transition: all 0.9s ease 0ms;
    }

    .helloFinal {
        left: 100%;
    }

    .initialGrayBox {
        font-family: GilroyExtraBold;
    	width:60%;
    	background-color: #1c1b20;
        position: absolute;
        top: 30%;
        right: -100%;
        transition: all 1.5s ease 0ms;
        color: #f4f4f4;
    }

    .finalGrayBox {
        right: 0%;
    }

    .skillGrid {
        margin:0 auto;
        width: 80%;
        height: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        place-items: stretch;
    /*    grid-template-rows: 1fr 1fr;*/
        grid-column-gap: 20px;
        grid-row-gap: 20px;
    }

    h3 {
        font-size: 2rem;
    }

    h3 + p {
        font-size: 1.5rem;
        color: #4c4a57;
    }
/*End Header Css*/

/*About CSS */ 

    .boxAbout {
        padding: 20px 0px 20px 0px;
        background-color: #f4f4f4;
    }

    .aboutContent {
        padding: 2rem 1rem 0 3rem;
    }

    .fixed {
    	/*position: fixed;*/
    }

    #parallaxAbout {
        font-size: 3vw;
        font-family: GilroyExtraBold;
        background-image: url(/guillermoroman/static/media/headerPicture.bc7dd37d.jpg);
        padding-Top: 50vh;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .aboutParagraph {
        width: 49%;
        display: inline-block;
        font-size: 2rem;
    }
/*END About CSS*/

/*Portfolio CSS*/
    #Portfolio {
        margin-top: -20px;
        padding: 13rem 30px 20px 3rem;
        display: grid;
        background-color:#f4f4f4;
        grid-row-gap: 50px;
    }

    .title {
        color: #1c1b20;
        line-height: 0.9;
        font-size: 6rem;
        font-weight: bold;
        font-family: GilroyExtraBold;
    }

    .pushRight {
        margin-left: 20%;
    }

    .pushRight2 {
        margin-left: 55%;
    }

    .pushLeft {
        float: right;
        margin-right: 10%;
    }
    .content {
        overflow: auto;
        overflow-y: hidden;
    }

    .portfolioParagraph {
        font-family: 'Libre Baskerville', serif;
        font-size: 2.2rem;
        line-height: 0.9;
    }

    .portfolioParagraph2 {
        font-family: GilroyExtraBold;
        font-style: normal;
        font-size: 1.8rem;
    }

    .portfolioParagraph3 {
        font-family: GilroyLight;
        font-size: 1rem;
    }

    .portfolioImage {
        max-width: 95%;
    }
    .link {
        position: relative;
        display: inline-block;
        font-family: "Libre Baskerville", serif;
        font-size: 1rem;
        font-style: italic;
        color: inherit !important;
        text-decoration: underline !important;
        background: none;
        border: none;
    }
    .linkHover:hover {
        transition: all ease-in 0.5s;
        background: #ff5851;
    }

    .paddingLink {
        margin-left: 5%;
    }

    .project-info {
        display: block;
    }
    .project-name {
        font-family: "Montserrat", sans-serif;
        color: #1c1b20;
        text-rendering: optimizeLegibility;
        font-size: 0.7rem;
        font-weight: 900;
        line-height: 1.5;
    }
    .project-data {
        font-family: "Libre Baskerville", serif;
        line-height: 1.5;
        text-rendering: optimizeLegibility;
        font-size: 0.7rem;
        color: #babcc1;
        font-style: italic;
    }
/*End Potfolio CSS*/

/* Upcoming CSS*/
    #Upcoming{
        margin-top: -20px;
        padding: 5rem 2rem 3rem 2rem;
        background-color: #f4f4f4;
    }
/*End Upcoming CSS*/

/*Grid Css*/
    .item-a {
      grid-area: header;
    }
    .item-b {
      grid-area: main;
    }
    .item-c {
      grid-area: sidebar;
    }
    .item-d {
      grid-area: footer;
    }
    .item-e {
      grid-area: info;
    }

    .container {
      display: grid;
      grid-template-columns: 1fr 1fr 1.5fr 0.5fr;
      grid-template-rows: auto;
      grid-template-areas: 
        ". . header header"
        "main main sidebar ."
        "main main info ."
        ". footer . .";
    }
    .container2 {
      display: grid;
      grid-template-columns: 0.5fr 1.5fr 0.2fr 1.20fr 1fr;
      grid-template-rows: auto;
      grid-template-areas: 
        ". header header . ."
        ". sidebar . main main"
        ". info . main main"
        ". . . footer .";
    }
    .container3 {
      display: grid;
      grid-column-gap: 1%;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto;
      grid-template-areas: 
        "header header header"
        "main main main";
    }
/*End Grid CSS*/

/*Grid CSS Media*/
   @media (max-width: 700px) {
        .container3 {
            display: grid;
            grid-row-gap: 2%;
            grid-template-columns: 1fr;
            grid-template-rows: auto;
            grid-template-areas: 
                "header"
                "main";
        }
        .portfolioImage {
        max-width: 100%;
        }

        .item-d {
            grid-area: footer;
            justify-self: end;
        }
        .container {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: auto;
            grid-row-gap: 5px;
            grid-template-areas: 
                "header header header header"
                "sidebar sidebar sidebar sidebar"
                "info info . ."
                ". . . ."
                "main main main main"
                ". . footer footer";
        }
        .container2 {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: auto;
            grid-row-gap: 5px;
            grid-template-areas: 
                "header header header header"
                "sidebar sidebar sidebar sidebar"
                "info info . ."
                ". . . ."
                "main main main main"
                ". . footer footer";
        }
   } 
/*End Grid Css Media*/

/*Media CSS*/
    @media (max-width: 1100px) {
        h3 {
            font-size: 1.4rem;
        }

        h3 + p {
            font-size: 1.1rem;
        }
        #parallaxAbout {
            padding-top: 35vh;
        }
        .expandRedBox {
            height:95vh;
        }
        .aboutParagraph {
            font-size: 1.9rem;
        }

        .boxAbout {
            font-size: 2rem;
            height: 80vh; 
        }
        #Portfolio {
        padding: 7rem 30px 20px 3rem;
        }
    }

    @media (max-width: 900px) {
        .aboutParagraph {
            font-size: 1.3rem;
        }
        .title {
            font-size: 5rem;
        }
    }

    @media (max-width: 700px) {
        h3 {
            font-size: 1.2rem;
        }

        h3 + p {
            font-size: 1rem;
        }
        .boxAbout {
            font-size: 1.9rem;
            height: 80vh; 
        }
        .aboutParagraph {
            width: 100%;
            font-size: 1.5rem;
            margin-top: -20px;
        }
        .title {
            font-size: 4rem;
        }
    }

    @media (max-width: 600px) {
        h3 {
            font-size: 1.1rem;
        }

        h3 + p {
            font-size: 0.9rem;
        }
        .welcomeMessage {
            font-size: 1rem;
        }

        .expandRedBox {
            height:100vh;
        }

        #parallaxAbout {
            font-size: 5vw;
            padding-top: 25vh;
            background-image: url(/guillermoroman/static/media/landing02.bf1e2b9b.jpg);
        }

        .aboutParagraph {
            width: 100%;
            margin-top: -10px;
            font-size: 1.3rem;
            display: block;
        }
        .boxAbout {
            font-size: 3.8vh;
            height: 80vh; 
        }

        .title {
            font-size: 3rem;
        }

        .pushRight {
        padding-left: 5%;
        }

        .pushRight2 {
            padding-left: 5%;
        }
        #Portfolio {
        padding: 3rem 15px 20px 15px;
        }
        .aboutContent {
        padding: 2rem 15px 0 15px;
        }
    }
/*End Media CSS*/
